.App {
  padding: 1rem;
  text-align: center;
  background-color: hsla(0,0%,0%,0.05);
  height: 100vh;
  overflow-y: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App .segment {
  width: 100%;
  max-width: 30rem;
  min-height: 30rem;
}

.App .segment .fluid.button {
  margin-bottom: 0.5rem;
}

.App .segment .form {
  text-align: left;
  display: inline-block;
}
